
body {
  height: 100vh;
  background-color: white;
}
#app {
  height: 100vh;
  padding: 16px 24px 8px 24px;
  background-color: #ffffff;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  border-radius: 6px;
}
#app .calc-display {
  border-bottom: 1px solid #eee;
  height: 40%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
#app .calc-display .content {
  position: relative;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  text-align: right;
  word-break: break-all;
  font-size: 24px;
  color: #aaa;
}
#app .calc-display .content div {
  position: absolute;
  right: 0;
  bottom: 0;
}
#app .calc-display .result {
  display: inline-block;
  padding: 8px 0;
  height: 36%;
  text-align: right;
  font-size: 55px;
  overflow: hidden;
  outline: none;
  color: #414141;
  word-break: break-all;
}
#app .calc-input {
  height: 60%;
}
#app .calc-input ul {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: space-around;
      -ms-flex-align: space-around;
          align-items: space-around;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  font-size: 22px;
  color: #aaa;
}
#app .calc-input ul li ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
#app .calc-input ul li ul li {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: 1;
          flex-flow: 1;
  width: 40px;
  height: 40px;
  text-align: center;
  line-height: 48px;
}
.plus,
.minus,
.divide,
.multiply {
  color: #f06e6e;
}
.equal {
  color: #ffffff;
  background-color: #f06e6e;
  border-radius: 50%;
  -webkit-box-shadow: 2px 3px 12px 1px #e5a2a0;
          box-shadow: 2px 3px 12px 1px #e5a2a0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
* {
    padding: 0;
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
  
  *::after,
  *::before {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
  }
  
  a {
    color: inherit;
    text-decoration: none;
  }
  
  ol,ul {
    list-style: none;
  }